'use client';

import { createContext, type ReactNode, useContext, useMemo } from 'react';

/**
 * ÁîªÂÉè„Ç¢„ÉÉ„Éó„É≠„Éº„ÉâÁµêÊûú
 */
export interface ImageUploadResult {
  /** ÁîªÂÉè„ÅÆURLÔºàË°®Á§∫Áî®Ôºâ */
  url: string;
  /** ÁîªÂÉè„ÅÆÂπÖÔºàÂèñÂæó„Åß„Åç„ÅüÂ†¥ÂêàÔºâ */
  width?: number;
  /** ÁîªÂÉè„ÅÆÈ´ò„ÅïÔºàÂèñÂæó„Åß„Åç„ÅüÂ†¥ÂêàÔºâ */
  height?: number;
}

/**
 * ÁîªÂÉè„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ„Éè„É≥„Éâ„É©„Éº„ÅÆÂûã
 */
export interface ImageUploadHandler {
  /**
   * ÁîªÂÉè„Éï„Ç°„Ç§„É´„Çí„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ„Åô„Çã
   * @param file - „Ç¢„ÉÉ„Éó„É≠„Éº„Éâ„Åô„Çã„Éï„Ç°„Ç§„É´
   * @returns „Ç¢„ÉÉ„Éó„É≠„Éº„ÉâÁµêÊûúÔºàURLÁ≠âÔºâ
   * @throws „Ç¢„ÉÉ„Éó„É≠„Éº„ÉâÂ§±ÊïóÊôÇ„ÅØ„Ç®„É©„Éº„Çí„Çπ„É≠„Éº
   */
  uploadImage: (file: File) => Promise<ImageUploadResult>;
}

/**
 * ÁîªÂÉè„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ„Ç≥„É≥„ÉÜ„Ç≠„Çπ„Éà„ÅÆÂÄ§
 */
interface ImageUploadContextValue {
  handler: ImageUploadHandler | null;
}

const ImageUploadContext = createContext<ImageUploadContextValue>({
  handler: null,
});

/**
 * ÁîªÂÉè„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ„Éè„É≥„Éâ„É©„Éº„ÇíÂèñÂæó„Åô„Çã„Éï„ÉÉ„ÇØ
 * @returns „Éè„É≥„Éâ„É©„ÉºÔºàÊú™Ë®≠ÂÆö„ÅÆÂ†¥Âêà„ÅØnullÔºâ
 */
export function useImageUpload(): ImageUploadHandler | null {
  const context = useContext(ImageUploadContext);
  return context.handler;
}

/**
 * ÁîªÂÉè„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ„Ç≥„É≥„ÉÜ„Ç≠„Çπ„Éà„ÅÆ„Éó„É≠„Éê„Ç§„ÉÄ„Éº
 */
interface ImageUploadProviderProps {
  children: ReactNode;
  handler: ImageUploadHandler | null;
}

export function ImageUploadProvider({ children, handler }: ImageUploadProviderProps) {
  const value = useMemo(() => ({ handler }), [handler]);
  return <ImageUploadContext.Provider value={value}>{children}</ImageUploadContext.Provider>;
}
