'use client';

import { AutoFocusPlugin } from '@lexical/react/LexicalAutoFocusPlugin';
import { CharacterLimitPlugin } from '@lexical/react/LexicalCharacterLimitPlugin';
import { CheckListPlugin } from '@lexical/react/LexicalCheckListPlugin';
import { ClearEditorPlugin } from '@lexical/react/LexicalClearEditorPlugin';
import { ClickableLinkPlugin } from '@lexical/react/LexicalClickableLinkPlugin';
import { useLexicalComposerContext } from '@lexical/react/LexicalComposerContext';
import { LexicalErrorBoundary } from '@lexical/react/LexicalErrorBoundary';
import { HashtagPlugin } from '@lexical/react/LexicalHashtagPlugin';
import { HistoryPlugin } from '@lexical/react/LexicalHistoryPlugin';
import { ListPlugin } from '@lexical/react/LexicalListPlugin';
import { RichTextPlugin } from '@lexical/react/LexicalRichTextPlugin';
import { SelectionAlwaysOnDisplay } from '@lexical/react/LexicalSelectionAlwaysOnDisplay';
import { TabIndentationPlugin } from '@lexical/react/LexicalTabIndentationPlugin';
import { TablePlugin } from '@lexical/react/LexicalTablePlugin';
import { useLexicalEditable } from '@lexical/react/useLexicalEditable';
import { CAN_USE_DOM } from '@lexical/utils';
import { useEffect, useState } from 'react';

import { useSettings } from '../context/SettingsContext';
import { useSharedHistoryContext } from '../context/SharedHistoryContext';
import AutocompletePlugin from '../plugins/AutocompletePlugin';
import AutoEmbedPlugin from '../plugins/AutoEmbedPlugin';
import AutoLinkPlugin from '../plugins/AutoLinkPlugin';
import CodeActionMenuPlugin from '../plugins/CodeActionMenuPlugin';
import CodeHighlightPrismPlugin from '../plugins/CodeHighlightPrismPlugin';
import CodeHighlightShikiPlugin from '../plugins/CodeHighlightShikiPlugin';
import CollapsiblePlugin from '../plugins/CollapsiblePlugin';
import ComponentPickerPlugin from '../plugins/ComponentPickerPlugin';
import ContextMenuPlugin from '../plugins/ContextMenuPlugin';
import DateTimePlugin from '../plugins/DateTimePlugin';
import DragDropPaste from '../plugins/DragDropPastePlugin';
import DraggableBlockPlugin from '../plugins/DraggableBlockPlugin';
import EmojiPickerPlugin from '../plugins/EmojiPickerPlugin';
import EmojisPlugin from '../plugins/EmojisPlugin';
import EquationsPlugin from '../plugins/EquationsPlugin';
import FigmaPlugin from '../plugins/FigmaPlugin';
import FloatingLinkEditorPlugin from '../plugins/FloatingLinkEditorPlugin';
import FragmentLinkPlugin from '../plugins/FragmentLinkPlugin';
import FloatingTextFormatToolbarPlugin from '../plugins/FloatingTextFormatToolbarPlugin';
import { HorizontalRulePlugin } from '../plugins/HorizontalRulePlugin';
import ImagesPlugin from '../plugins/ImagesPlugin';
import { LayoutPlugin } from '../plugins/LayoutPlugin/LayoutPlugin';
import LinkPlugin from '../plugins/LinkPlugin';
import MarkdownPastePlugin from '../plugins/MarkdownPastePlugin';
import MarkdownShortcutPlugin from '../plugins/MarkdownShortcutPlugin';
import { MaxLengthPlugin } from '../plugins/MaxLengthPlugin';
import MentionsPlugin from '../plugins/MentionsPlugin';
import PageBreakPlugin from '../plugins/PageBreakPlugin';
import ShortcutsPlugin from '../plugins/ShortcutsPlugin';
import SpecialTextPlugin from '../plugins/SpecialTextPlugin';
import TabFocusPlugin from '../plugins/TabFocusPlugin';
import TableCellActionMenuPlugin from '../plugins/TableActionMenuPlugin';
import TableCellResizer from '../plugins/TableCellResizer';
import TableHoverActionsPlugin from '../plugins/TableHoverActionsPlugin';
import TableOfContentsPlugin from '../plugins/TableOfContentsPlugin';
import ToolbarPlugin from '../plugins/ToolbarPlugin';
import TwitterPlugin from '../plugins/TwitterPlugin';
import YouTubePlugin from '../plugins/YouTubePlugin';
import ContentEditable from '../ui/ContentEditable';

export default function Editor({ isFullscreen = false }: { isFullscreen?: boolean }) {
  const { historyState } = useSharedHistoryContext();
  const {
    settings: {
      isCodeHighlighted,
      isCodeShiki,
      isAutocomplete,
      isMaxLength,
      isCharLimit,
      autoFocus,
      hasLinkAttributes,
      hasNestedTables: hasTabHandler,
      isCharLimitUtf8,
      showTableOfContents,
      showToolbar,
      shouldUseLexicalContextMenu,
      tableCellMerge,
      tableCellBackgroundColor,
      tableHorizontalScroll,
      shouldAllowHighlightingWithBrackets,
      selectionAlwaysOnDisplay,
      listStrictIndent,
    },
  } = useSettings();
  const isEditable = useLexicalEditable();
  const placeholder = 'ÊñáÁ´†„ÇíÂÖ•Âäõ„Åô„ÇãÂâç„Å´ÊòØÈùû„Äå/„Äç„ÇíÂÖ•Âäõ„Åó„Å¶„Åø„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ';
  const [floatingAnchorElem, setFloatingAnchorElem] = useState<HTMLDivElement | null>(null);
  const [isSmallWidthViewport, setIsSmallWidthViewport] = useState<boolean>(false);
  const [editor] = useLexicalComposerContext();
  const [activeEditor, setActiveEditor] = useState(editor);
  const [isLinkEditMode, setIsLinkEditMode] = useState<boolean>(false);

  const onRef = (_floatingAnchorElem: HTMLDivElement) => {
    if (_floatingAnchorElem !== null) {
      setFloatingAnchorElem(_floatingAnchorElem);
    }
  };

  useEffect(() => {
    const updateViewPortWidth = () => {
      const isNextSmallWidthViewport = CAN_USE_DOM && window.matchMedia('(max-width: 1025px)').matches;

      if (isNextSmallWidthViewport !== isSmallWidthViewport) {
        setIsSmallWidthViewport(isNextSmallWidthViewport);
      }
    };
    updateViewPortWidth();
    window.addEventListener('resize', updateViewPortWidth);

    return () => {
      window.removeEventListener('resize', updateViewPortWidth);
    };
  }, [isSmallWidthViewport]);

  return (
    <>
      {showToolbar && (
        <ToolbarPlugin
          editor={editor}
          activeEditor={activeEditor}
          setActiveEditor={setActiveEditor}
          setIsLinkEditMode={setIsLinkEditMode}
        />
      )}
      {showToolbar && <ShortcutsPlugin editor={activeEditor} setIsLinkEditMode={setIsLinkEditMode} />}
      <div className={`editor-container ${isFullscreen ? 'flex-1 overflow-auto' : ''}`}>
        {isMaxLength && <MaxLengthPlugin maxLength={30} />}
        <DragDropPaste />
        {autoFocus && <AutoFocusPlugin />}
        {selectionAlwaysOnDisplay && <SelectionAlwaysOnDisplay />}
        <ClearEditorPlugin />
        <ComponentPickerPlugin />
        <EmojiPickerPlugin />
        <AutoEmbedPlugin />
        <MentionsPlugin />
        <EmojisPlugin />
        <HashtagPlugin />
        <AutoLinkPlugin />
        <DateTimePlugin />
        <HistoryPlugin externalHistoryState={historyState} />
        <RichTextPlugin
          contentEditable={
            <div className="editor-scroller">
              <div className="editor" ref={onRef}>
                <ContentEditable placeholder={placeholder} placeholderClassName="editor-placeholder" />
              </div>
            </div>
          }
          ErrorBoundary={LexicalErrorBoundary}
        />
        <MarkdownShortcutPlugin />
        <MarkdownPastePlugin />
        {isCodeHighlighted && (isCodeShiki ? <CodeHighlightShikiPlugin /> : <CodeHighlightPrismPlugin />)}
        <ListPlugin hasStrictIndent={listStrictIndent} />
        <CheckListPlugin />
        <TablePlugin
          hasCellMerge={tableCellMerge}
          hasCellBackgroundColor={tableCellBackgroundColor}
          hasHorizontalScroll={tableHorizontalScroll}
          hasTabHandler={hasTabHandler}
        />
        <TableCellResizer />
        <ImagesPlugin />
        <LinkPlugin hasLinkAttributes={hasLinkAttributes} />
        <TwitterPlugin />
        <YouTubePlugin />
        <FigmaPlugin />
        <ClickableLinkPlugin disabled={isEditable} />
        <FragmentLinkPlugin />
        <HorizontalRulePlugin />
        <EquationsPlugin />
        <TabFocusPlugin />
        <TabIndentationPlugin maxIndent={7} />
        <CollapsiblePlugin />
        <PageBreakPlugin />
        <LayoutPlugin />
        {floatingAnchorElem && (
          <>
            <FloatingLinkEditorPlugin
              anchorElem={floatingAnchorElem}
              isLinkEditMode={isLinkEditMode}
              setIsLinkEditMode={setIsLinkEditMode}
            />
            <TableCellActionMenuPlugin anchorElem={floatingAnchorElem} cellMerge={true} />
          </>
        )}
        {floatingAnchorElem && !isSmallWidthViewport && (
          <>
            <DraggableBlockPlugin anchorElem={floatingAnchorElem} />
            <CodeActionMenuPlugin anchorElem={floatingAnchorElem} />
            <TableHoverActionsPlugin anchorElem={floatingAnchorElem} />
            <FloatingTextFormatToolbarPlugin anchorElem={floatingAnchorElem} setIsLinkEditMode={setIsLinkEditMode} />
          </>
        )}
        {(isCharLimit || isCharLimitUtf8) && (
          <CharacterLimitPlugin charset={isCharLimit ? 'UTF-16' : 'UTF-8'} maxLength={5} />
        )}
        {isAutocomplete && <AutocompletePlugin />}
        <div>{showTableOfContents && <TableOfContentsPlugin />}</div>
        {shouldUseLexicalContextMenu && <ContextMenuPlugin />}
        {shouldAllowHighlightingWithBrackets && <SpecialTextPlugin />}
      </div>
    </>
  );
}
