/**
 * MarkdownPastePlugin
 *
 * „Éû„Éº„ÇØ„ÉÄ„Ç¶„É≥„ÉÜ„Ç≠„Çπ„Éà„Çí„Éö„Éº„Çπ„Éà„Åó„ÅüÈöõ„Å´„ÄÅLexical „Éé„Éº„Éâ„Å´Â§âÊèõ„Åó„Å¶„Ç§„É≥„Çµ„Éº„Éà„Åô„Çã„Éó„É©„Ç∞„Ç§„É≥„ÄÇ
 * ÈÄöÂ∏∏„ÅÆ„ÉÜ„Ç≠„Çπ„Éà„Éö„Éº„Çπ„ÉàÔºàCode „Éñ„É≠„ÉÉ„ÇØ„Å®„Åó„Å¶Ë™çË≠ò„Åï„Çå„Å¶„Åó„Åæ„ÅÜÂïèÈ°åÔºâ„ÇíÂõûÈÅø„Åô„Çã„ÄÇ
 *
 * @see https://github.com/facebook/lexical/issues/7663
 * @see https://payloadcms.com/docs/rich-text/converting-markdown#markdown-to-richtext
 */

import { $convertFromMarkdownString } from '@lexical/markdown';
import { useLexicalComposerContext } from '@lexical/react/LexicalComposerContext';
import {
  $createParagraphNode,
  $getSelection,
  $isRangeSelection,
  COMMAND_PRIORITY_HIGH,
  type LexicalNode,
  PASTE_COMMAND,
} from 'lexical';
import { useCallback, useEffect } from 'react';
import { normalizeListIndentation } from '../../transformers/markdown-transformers';
import { PLAYGROUND_TRANSFORMERS } from '../MarkdownTransformers';

/**
 * „Éû„Éº„ÇØ„ÉÄ„Ç¶„É≥„Çâ„Åó„ÅÑ„Éë„Çø„Éº„É≥„ÇíÊ§úÂá∫„Åô„ÇãÊ≠£Ë¶èË°®ÁèæÁæ§
 */
const MARKDOWN_PATTERNS = {
  // „Éò„ÉÉ„ÉÄ„Éº: # Header, ## Header, ### Header
  heading: /^#{1,6}\s+.+/m,
  // Â§™Â≠ó: **text** or __text__
  bold: /\*\*[^*]+\*\*|__[^_]+__/,
  // „Ç§„Çø„É™„ÉÉ„ÇØ: *text* or _text_ (Â§™Â≠ó„Åß„Å™„ÅÑ„ÇÇ„ÅÆ)
  italic: /(?<!\*)\*(?!\*)[^*]+\*(?!\*)|(?<!_)_(?!_)[^_]+_(?!_)/,
  // ÁÆáÊù°Êõ∏„Åç„É™„Çπ„Éà: - item, * item, + item
  unorderedList: /^[\s]*[-*+]\s+.+/m,
  // Áï™Âè∑‰ªò„Åç„É™„Çπ„Éà: 1. item, 2. item
  orderedList: /^[\s]*\d+\.\s+.+/m,
  // „Ç≥„Éº„Éâ„Éñ„É≠„ÉÉ„ÇØ: ```code```
  codeBlock: /```[\s\S]*?```/,
  // „Ç§„É≥„É©„Ç§„É≥„Ç≥„Éº„Éâ: `code`
  inlineCode: /`[^`]+`/,
  // „É™„É≥„ÇØ: [text](url)
  link: /\[([^\]]+)\]\(([^)]+)\)/,
  // ÁîªÂÉè: ![alt](url)
  image: /!\[([^\]]*)\]\(([^)]+)\)/,
  // ÂºïÁî®: > quote
  blockquote: /^>\s+.+/m,
  // Ê∞¥Âπ≥Á∑ö: ---, ***, ___
  horizontalRule: /^(---|\*\*\*|___)\s*$/m,
  // „ÉÅ„Çß„ÉÉ„ÇØ„É™„Çπ„Éà: - [ ] or - [x]
  checklist: /^[\s]*-\s+\[([ xX])\]\s+.+/m,
  // „ÉÜ„Éº„Éñ„É´: | col1 | col2 |
  table: /^\|.+\|$/m,
};

/**
 * „ÉÜ„Ç≠„Çπ„Éà„Åå„Éû„Éº„ÇØ„ÉÄ„Ç¶„É≥„Åã„Å©„ÅÜ„Åã„ÇíÂà§ÂÆö„Åô„Çã
 * Ë§áÊï∞„ÅÆ„Éë„Çø„Éº„É≥„Åå„Éû„ÉÉ„ÉÅ„Åô„Çã„Åª„Å©„ÄÅ„Éû„Éº„ÇØ„ÉÄ„Ç¶„É≥„ÅÆÂèØËÉΩÊÄß„ÅåÈ´ò„ÅÑ
 */
function isLikelyMarkdown(text: string): boolean {
  // Á©∫ÊñáÂ≠ó„ÇÑ„Ç≥„Éº„Éâ„Å£„ÅΩ„ÅÑ„ÉÜ„Ç≠„Çπ„ÉàÔºà{}„ÇÑ;„ÅåÂ§ö„ÅÑÔºâ„ÅØÈô§Â§ñ
  if (!text || text.trim().length === 0) {
    return false;
  }

  // Ë§áÊï∞Ë°å„Åß„Å™„ÅÑÁü≠„ÅÑ„ÉÜ„Ç≠„Çπ„Éà„ÅØÈÄöÂ∏∏„ÅÆ„Éö„Éº„Çπ„Éà„Å®„Åó„Å¶Êâ±„ÅÜ
  const lines = text.split('\n');
  if (lines.length === 1 && text.length < 50) {
    // Âçò‰∏ÄË°å„Åß„ÇÇÊòéÁ¢∫„Å™„Éû„Éº„ÇØ„ÉÄ„Ç¶„É≥„Éë„Çø„Éº„É≥„Åå„ÅÇ„Çå„Å∞OK
    const hasHeading = MARKDOWN_PATTERNS.heading.test(text);
    const hasBold = MARKDOWN_PATTERNS.bold.test(text);
    const hasItalic = MARKDOWN_PATTERNS.italic.test(text);
    const hasLink = MARKDOWN_PATTERNS.link.test(text);
    const hasInlineCode = MARKDOWN_PATTERNS.inlineCode.test(text);

    if (hasHeading || hasBold || hasItalic || hasLink || hasInlineCode) {
      return true;
    }
    return false;
  }

  // „Éû„ÉÉ„ÉÅ„Åó„Åü„Éë„Çø„Éº„É≥Êï∞„Çí„Ç´„Ç¶„É≥„Éà
  let matchCount = 0;

  if (MARKDOWN_PATTERNS.heading.test(text)) matchCount++;
  if (MARKDOWN_PATTERNS.bold.test(text)) matchCount++;
  if (MARKDOWN_PATTERNS.italic.test(text)) matchCount++;
  if (MARKDOWN_PATTERNS.unorderedList.test(text)) matchCount++;
  if (MARKDOWN_PATTERNS.orderedList.test(text)) matchCount++;
  if (MARKDOWN_PATTERNS.codeBlock.test(text)) matchCount++;
  if (MARKDOWN_PATTERNS.inlineCode.test(text)) matchCount++;
  if (MARKDOWN_PATTERNS.link.test(text)) matchCount++;
  if (MARKDOWN_PATTERNS.image.test(text)) matchCount++;
  if (MARKDOWN_PATTERNS.blockquote.test(text)) matchCount++;
  if (MARKDOWN_PATTERNS.horizontalRule.test(text)) matchCount++;
  if (MARKDOWN_PATTERNS.checklist.test(text)) matchCount++;
  if (MARKDOWN_PATTERNS.table.test(text)) matchCount++;

  // 2„Å§‰ª•‰∏ä„ÅÆ„Éë„Çø„Éº„É≥„Åå„Éû„ÉÉ„ÉÅ„Åô„Çã„Åã„ÄÅÂº∑„ÅÑ„Éë„Çø„Éº„É≥Ôºà„Éò„ÉÉ„ÉÄ„Éº„ÄÅ„Ç≥„Éº„Éâ„Éñ„É≠„ÉÉ„ÇØ„ÄÅ„É™„Çπ„ÉàÔºâ„Åå„ÅÇ„Çå„Å∞„Éû„Éº„ÇØ„ÉÄ„Ç¶„É≥„Å®Âà§Êñ≠
  const hasStrongPattern =
    MARKDOWN_PATTERNS.heading.test(text) ||
    MARKDOWN_PATTERNS.codeBlock.test(text) ||
    MARKDOWN_PATTERNS.unorderedList.test(text) ||
    MARKDOWN_PATTERNS.orderedList.test(text) ||
    MARKDOWN_PATTERNS.blockquote.test(text) ||
    MARKDOWN_PATTERNS.table.test(text);

  return matchCount >= 2 || hasStrongPattern;
}

/**
 * ClipboardEvent „Åã„Çâ„Éó„É¨„Éº„É≥„ÉÜ„Ç≠„Çπ„Éà„ÇíÂèñÂæó
 */
function getPlainTextFromClipboard(event: ClipboardEvent): string | null {
  const clipboardData = event.clipboardData;
  if (!clipboardData) {
    return null;
  }

  // HTML „Åå„ÅÇ„ÇãÂ†¥Âêà„ÅØÈÄöÂ∏∏„ÅÆ„Éö„Éº„Çπ„ÉàÂá¶ÁêÜ„Å´‰ªª„Åõ„ÇãÔºàWord/„É™„ÉÉ„ÉÅ„ÉÜ„Ç≠„Çπ„Éà„Åã„Çâ„ÅÆ„Ç≥„Éî„Éº„Å™„Å©Ôºâ
  const htmlData = clipboardData.getData('text/html');
  if (htmlData && htmlData.trim().length > 0) {
    // „Åü„Å†„Åó„ÄÅHTML „ÅåÂçòÁ¥î„Å™„Éó„É¨„Éº„É≥„ÉÜ„Ç≠„Çπ„Éà„É©„ÉÉ„Éë„Éº„ÅÆÂ†¥Âêà„ÅØ„Éó„É¨„Éº„É≥„ÉÜ„Ç≠„Çπ„Éà„ÇíÂÑ™ÂÖà
    // ‰æã: <meta charset="utf-8"><span>...</span> „ÅÆ„Çà„ÅÜ„Å™„Ç∑„É≥„Éó„É´„Å™ÊßãÈÄ†
    const isSimpleHtml =
      /<(meta|span|div|p|br)[^>]*>/i.test(htmlData) && !/<(table|img|a|ul|ol|li|h[1-6])[^>]*>/i.test(htmlData);
    if (!isSimpleHtml) {
      return null;
    }
  }

  return clipboardData.getData('text/plain');
}

export default function MarkdownPastePlugin(): null {
  const [editor] = useLexicalComposerContext();

  const handlePaste = useCallback(
    (event: ClipboardEvent): boolean => {
      const plainText = getPlainTextFromClipboard(event);

      if (!plainText) {
        // „Éó„É¨„Éº„É≥„ÉÜ„Ç≠„Çπ„Éà„Åå„Å™„ÅÑÂ†¥Âêà„ÅØÈÄöÂ∏∏„ÅÆ„Éö„Éº„Çπ„ÉàÂá¶ÁêÜ„Å´ÂßîË≠≤
        return false;
      }

      // „Éû„Éº„ÇØ„ÉÄ„Ç¶„É≥„Åã„Å©„ÅÜ„ÅãÂà§ÂÆö
      if (!isLikelyMarkdown(plainText)) {
        // „Éû„Éº„ÇØ„ÉÄ„Ç¶„É≥„Åß„Å™„ÅÑÂ†¥Âêà„ÅØÈÄöÂ∏∏„ÅÆ„Éö„Éº„Çπ„ÉàÂá¶ÁêÜ„Å´ÂßîË≠≤
        return false;
      }

      // „Éû„Éº„ÇØ„ÉÄ„Ç¶„É≥„Å®„Åó„Å¶Âá¶ÁêÜ
      event.preventDefault();

      editor.update(() => {
        const selection = $getSelection();

        if (!$isRangeSelection(selection)) {
          return;
        }

        // ÈÅ∏ÊäûÁØÑÂõ≤„ÇíÂâäÈô§ÔºàÈÅ∏Êäû„ÉÜ„Ç≠„Çπ„Éà„ÇíÁΩÆÊèõ„Åô„ÇãÂ†¥ÂêàÔºâ
        selection.removeText();

        // ÁèæÂú®„ÅÆ„Éé„Éº„Éâ„ÇíÂèñÂæó
        const anchorNode = selection.anchor.getNode();

        // Á©∫„ÅÆÊÆµËêΩ„Éé„Éº„Éâ„Çí‰ΩúÊàê„Åó„Å¶„Éû„Éº„ÇØ„ÉÄ„Ç¶„É≥„ÇíÂ§âÊèõ
        // $convertFromMarkdownString „ÅØÂØæË±°„Éé„Éº„Éâ„ÅÆÂ≠ê„Å®„Åó„Å¶Â§âÊèõÁµêÊûú„ÇíËøΩÂä†„Åô„Çã
        const paragraphNode = $createParagraphNode();

        // „Éû„Éº„ÇØ„ÉÄ„Ç¶„É≥„Çí Lexical „Éé„Éº„Éâ„Å´Â§âÊèõÔºà2„Çπ„Éö„Éº„Çπ„Ç§„É≥„Éá„É≥„Éà„Çí4„Çπ„Éö„Éº„Çπ„Å´Ê≠£Ë¶èÂåñÔºâ
        $convertFromMarkdownString(normalizeListIndentation(plainText), PLAYGROUND_TRANSFORMERS, paragraphNode, true);

        // Â§âÊèõ„Åï„Çå„Åü„Éé„Éº„Éâ„ÇíÁèæÂú®„ÅÆ‰ΩçÁΩÆ„Å´ÊåøÂÖ•
        const children = paragraphNode.getChildren();

        if (children.length > 0) {
          // ÁèæÂú®„ÅÆ„Éé„Éº„Éâ„ÅåÁ©∫„ÅÆÊÆµËêΩ„ÅÆÂ†¥Âêà„ÅØÁΩÆÊèõ„ÄÅ„Åù„ÅÜ„Åß„Å™„Åë„Çå„Å∞Âæå„Å´ÊåøÂÖ•
          const topLevelNode = anchorNode.getTopLevelElement();

          if (topLevelNode) {
            // ÂÖ®„Å¶„ÅÆÂ§âÊèõ„Åï„Çå„Åü„Éé„Éº„Éâ„ÇíÊåøÂÖ•
            let lastInserted: LexicalNode = topLevelNode;
            for (const child of children) {
              lastInserted.insertAfter(child);
              lastInserted = child;
            }

            // ÂÖÉ„ÅÆÁ©∫„ÅÆÊÆµËêΩ„ÇíÂâäÈô§ÔºàÂøÖË¶Å„Å´Âøú„Åò„Å¶Ôºâ
            if (topLevelNode.getTextContent().trim() === '') {
              topLevelNode.remove();
            }

            // „Ç´„Éº„ÇΩ„É´„ÇíÊúÄÂæå„Å´ÊåøÂÖ•„Åó„Åü„Éé„Éº„Éâ„ÅÆÊú´Â∞æ„Å´ÁßªÂãï
            lastInserted.selectEnd();
          } else {
            // „Éï„Ç©„Éº„É´„Éê„ÉÉ„ÇØ: Áõ¥Êé•ÊåøÂÖ•
            selection.insertNodes(children);
          }
        }
      });

      return true;
    },
    [editor],
  );

  useEffect(() => {
    // PASTE_COMMAND „ÇíÈ´òÂÑ™ÂÖàÂ∫¶„ÅßÁôªÈå≤„Åó„ÄÅ„Éû„Éº„ÇØ„ÉÄ„Ç¶„É≥„Éö„Éº„Çπ„Éà„ÇíÂÑ™ÂÖàÂá¶ÁêÜ
    return editor.registerCommand(
      PASTE_COMMAND,
      (event: ClipboardEvent) => {
        return handlePaste(event);
      },
      COMMAND_PRIORITY_HIGH,
    );
  }, [editor, handlePaste]);

  return null;
}
