/**
 * FragmentLinkPlugin
 *
 * #„ÅßÂßã„Åæ„Çã„Éï„É©„Ç∞„É°„É≥„Éà„É™„É≥„ÇØÔºàÁõÆÊ¨°„Å™„Å©Ôºâ„Çí„ÇØ„É™„ÉÉ„ÇØ„Åó„ÅüÈöõ„Å´„ÄÅ
 * ÂØæÂøú„Åô„ÇãË¶ãÂá∫„Åó„Å´„Çπ„ÇØ„É≠„Éº„É´„Åô„ÇãÂá¶ÁêÜ„ÇíË°å„ÅÜ„Éó„É©„Ç∞„Ç§„É≥„ÄÇ
 *
 * DOM „Ç§„Éô„É≥„Éà„É¨„Éô„É´„ÅßÂá¶ÁêÜ„Åô„Çã„Åì„Å®„Åß„ÄÅClickableLinkPlugin „Çà„ÇäÂÖà„Å´
 * „Éï„É©„Ç∞„É°„É≥„Éà„É™„É≥„ÇØ„Çí„Ç§„É≥„Çø„Éº„Çª„Éó„Éà„Åô„Çã„ÄÇ
 */
import { useLexicalComposerContext } from '@lexical/react/LexicalComposerContext';
import { $isHeadingNode } from '@lexical/rich-text';
import { $getRoot } from 'lexical';
import type { JSX } from 'react';
import { useEffect } from 'react';

/**
 * Ë¶ãÂá∫„Åó„ÉÜ„Ç≠„Çπ„Éà„Åã„ÇâslugÔºàIDÔºâ„ÇíÁîüÊàê
 * Markdown‰∫íÊèõ„ÅÆslugÁîüÊàê„É≠„Ç∏„ÉÉ„ÇØ
 */
function generateSlug(text: string): string {
  return text
    .toLowerCase()
    .trim()
    .replace(/\s+/g, '-')
    .replace(/[^\p{L}\p{N}\-]/gu, '')
    .replace(/-+/g, '-')
    .replace(/^-|-$/g, '');
}

/**
 * „ÉÜ„Ç≠„Çπ„Éà„ÇíÊ≠£Ë¶èÂåñÔºàÊØîËºÉÁî®Ôºâ
 */
function normalizeText(text: string): string {
  return text.toLowerCase().trim();
}

export default function FragmentLinkPlugin(): JSX.Element | null {
  const [editor] = useLexicalComposerContext();

  useEffect(() => {
    const rootElement = editor.getRootElement();
    if (!rootElement) {
      return;
    }

    const handleClick = (event: MouseEvent) => {
      const target = event.target as HTMLElement;
      const anchor = target.closest('a');

      if (!anchor) {
        return;
      }

      const href = anchor.getAttribute('href');

      // #„ÅßÂßã„Åæ„Çã„Éï„É©„Ç∞„É°„É≥„Éà„É™„É≥„ÇØ„ÅÆ„ÅøÂá¶ÁêÜ
      if (!href || !href.startsWith('#')) {
        return;
      }

      event.preventDefault();
      event.stopPropagation();

      const targetFragment = decodeURIComponent(href.slice(1)); // #„ÇíÈô§Âéª„Åó„Å¶„Éá„Ç≥„Éº„Éâ
      const targetSlug = generateSlug(targetFragment);
      const targetNormalized = normalizeText(targetFragment);

      // Ë¶ãÂá∫„Åó„Éé„Éº„Éâ„ÇíÊ§úÁ¥¢
      editor.getEditorState().read(() => {
        const root = $getRoot();

        for (const child of root.getChildren()) {
          if ($isHeadingNode(child)) {
            const headingText = child.getTextContent();
            const headingSlug = generateSlug(headingText);
            const headingNormalized = normalizeText(headingText);

            // slug‰∏ÄËá¥„ÄÅÊ≠£Ë¶èÂåñ„ÉÜ„Ç≠„Çπ„Éà‰∏ÄËá¥„ÄÅ„Åæ„Åü„ÅØÈÉ®ÂàÜ‰∏ÄËá¥„ÅßË¶ãÂá∫„Åó„ÇíË¶ã„Å§„Åë„Çã
            if (
              headingSlug === targetSlug ||
              headingNormalized === targetNormalized ||
              headingNormalized.includes(targetNormalized) ||
              targetNormalized.includes(headingNormalized)
            ) {
              const domElement = editor.getElementByKey(child.getKey());
              if (domElement) {
                domElement.scrollIntoView({ behavior: 'smooth', block: 'start' });
                return;
              }
            }
          }
        }
      });
    };

    // capture „Éï„Çß„Éº„Ç∫„ÅßÂá¶ÁêÜ„Åô„Çã„Åì„Å®„Åß ClickableLinkPlugin „Çà„ÇäÂÖà„Å´„Ç§„Éô„É≥„Éà„ÇíÊçïÊçâ
    rootElement.addEventListener('click', handleClick, true);

    return () => {
      rootElement.removeEventListener('click', handleClick, true);
    };
  }, [editor]);

  return null;
}
