/**
 * InsertMarkdownPlugin
 *
 * Â§ñÈÉ®„Åã„ÇâMarkdown„ÉÜ„Ç≠„Çπ„Éà„Çí„Ç®„Éá„Ç£„Çø„Å´ÊåøÂÖ•„Åô„Çã„Åü„ÇÅ„ÅÆ„Éó„É©„Ç∞„Ç§„É≥„ÄÇ
 * „Ç´„Çπ„Çø„É†„Ç≥„Éû„É≥„Éâ„Çí‰ΩøÁî®„Åó„Å¶„ÄÅÊó¢Â≠ò„ÅÆ„Ç≥„É≥„ÉÜ„É≥„ÉÑ„Çí‰øùÊåÅ„Åó„Åü„Åæ„Åæ
 * Markdown„Çí„Ç®„Éá„Ç£„Çø„ÅÆÊú´Â∞æ„Å´ËøΩË®ò„Åô„Çã„ÄÇ
 */

import { $convertFromMarkdownString, $convertToMarkdownString } from '@lexical/markdown';
import { useLexicalComposerContext } from '@lexical/react/LexicalComposerContext';
import { $getRoot, COMMAND_PRIORITY_EDITOR, createCommand, type LexicalCommand } from 'lexical';
import { useEffect } from 'react';
import { PLAYGROUND_TRANSFORMERS } from '../MarkdownTransformers';

/**
 * Markdown„ÇíÊåøÂÖ•„Åô„Çã„Åü„ÇÅ„ÅÆ„Ç´„Çπ„Çø„É†„Ç≥„Éû„É≥„Éâ
 */
export const INSERT_MARKDOWN_COMMAND: LexicalCommand<string> = createCommand('INSERT_MARKDOWN_COMMAND');

export default function InsertMarkdownPlugin(): null {
  const [editor] = useLexicalComposerContext();

  useEffect(() => {
    return editor.registerCommand<string>(
      INSERT_MARKDOWN_COMMAND,
      (markdown) => {
        if (!markdown || markdown.trim().length === 0) {
          return false;
        }

        editor.update(() => {
          // ÁèæÂú®„ÅÆ„Ç≥„É≥„ÉÜ„É≥„ÉÑ„ÇíMarkdown„Å®„Åó„Å¶ÂèñÂæó
          const currentMarkdown = $convertToMarkdownString(PLAYGROUND_TRANSFORMERS);

          // ÁèæÂú®„ÅÆ„Ç≥„É≥„ÉÜ„É≥„ÉÑ„Å®Êñ∞„Åó„ÅÑ„Éû„Éº„ÇØ„ÉÄ„Ç¶„É≥„ÇíÁµêÂêà
          const combinedMarkdown = currentMarkdown.trim()
            ? `${currentMarkdown.trim()}\n\n${markdown}`
            : markdown;

          // „É´„Éº„Éà„Çí„ÇØ„É™„Ç¢„Åó„Å¶ÁµêÂêà„Åó„Åü„Éû„Éº„ÇØ„ÉÄ„Ç¶„É≥„ÇíÂ§âÊèõ
          const root = $getRoot();
          root.clear();
          $convertFromMarkdownString(combinedMarkdown, PLAYGROUND_TRANSFORMERS);

          // ÊúÄÂæå„ÅÆ„Éé„Éº„Éâ„ÇíÈÅ∏Êäû
          const newLastChild = root.getLastChild();
          newLastChild?.selectEnd();
        });

        return true;
      },
      COMMAND_PRIORITY_EDITOR,
    );
  }, [editor]);

  return null;
}
