/**
 * „Ç®„Éá„Ç£„Çø„ÅÆÂÖ®ÁîªÈù¢„É¢„Éº„ÉâÁÆ°ÁêÜÁî®Context
 */
'use client';

import { createContext, type ReactNode, useCallback, useContext, useEffect, useMemo, useState } from 'react';

interface FullscreenContextType {
  isFullscreen: boolean;
  toggleFullscreen: () => void;
  exitFullscreen: () => void;
}

const FullscreenContext = createContext<FullscreenContextType | undefined>(undefined);

export function FullscreenProvider({ children }: { children: ReactNode }) {
  const [isFullscreen, setIsFullscreen] = useState(false);

  const toggleFullscreen = useCallback(() => {
    setIsFullscreen((prev) => !prev);
  }, []);

  const exitFullscreen = useCallback(() => {
    setIsFullscreen(false);
  }, []);

  // ESC„Ç≠„Éº„ÅßÂÖ®ÁîªÈù¢„É¢„Éº„ÉâÁµÇ‰∫Ü
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.key === 'Escape' && isFullscreen) {
        setIsFullscreen(false);
      }
    };

    if (isFullscreen) {
      document.addEventListener('keydown', handleKeyDown);
      // Ê≥®: body„ÅÆoverflowÂà∂Âæ°„ÅØË¶™„Ç≥„É≥„Éù„Éº„Éç„É≥„ÉàÔºà„É¢„Éº„ÉÄ„É´Á≠âÔºâ„Å´‰ªª„Åõ„Çã
      // ÂÖ®ÁîªÈù¢„Ç®„Éá„Ç£„Çø„ÅØfixed position„ÅßË°®Á§∫„Åï„Çå„Çã„Åü„ÇÅ„ÄÅËøΩÂä†„ÅÆÂà∂Âæ°„ÅØ‰∏çË¶Å
    }

    return () => {
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, [isFullscreen]);

  const value = useMemo(
    () => ({
      isFullscreen,
      toggleFullscreen,
      exitFullscreen,
    }),
    [isFullscreen, toggleFullscreen, exitFullscreen],
  );

  return <FullscreenContext.Provider value={value}>{children}</FullscreenContext.Provider>;
}

export function useFullscreen() {
  const context = useContext(FullscreenContext);
  if (context === undefined) {
    throw new Error('useFullscreen must be used within a FullscreenProvider');
  }
  return context;
}
