import { $createHorizontalRuleNode, $isHorizontalRuleNode, HorizontalRuleNode } from '@lexical/extension';
import {
  $convertFromMarkdownString,
  $convertToMarkdownString,
  CHECK_LIST,
  ELEMENT_TRANSFORMERS,
  type ElementTransformer,
  MULTILINE_ELEMENT_TRANSFORMERS,
  TEXT_FORMAT_TRANSFORMERS,
  TEXT_MATCH_TRANSFORMERS,
  type TextMatchTransformer,
  type Transformer,
} from '@lexical/markdown';
import {
  $createTableCellNode,
  $createTableNode,
  $createTableRowNode,
  $isTableCellNode,
  $isTableNode,
  $isTableRowNode,
  TableCellHeaderStates,
  TableCellNode,
  TableNode,
  TableRowNode,
} from '@lexical/table';
import { $createTextNode, $isParagraphNode, $isTextNode, type LexicalNode } from 'lexical';
import { $createEquationNode, $isEquationNode, EquationNode } from '../nodes/EquationNode';
import { $createImageNode, $isImageNode, ImageNode } from '../nodes/ImageNode';
import { $createTweetNode, $isTweetNode, TweetNode } from '../nodes/TweetNode';
import emojiList from '../utils/emoji-list';

export const HR: ElementTransformer = {
  dependencies: [HorizontalRuleNode],
  export: (node: LexicalNode) => {
    return $isHorizontalRuleNode(node) ? '***' : null;
  },
  regExp: /^(---|\*\*\*|___)\s?$/,
  replace: (parentNode, _1, _2, isImport) => {
    const line = $createHorizontalRuleNode();

    // TODO: Get rid of isImport flag
    if (isImport || parentNode.getNextSibling() != null) {
      parentNode.replace(line);
    } else {
      parentNode.insertBefore(line);
    }

    line.selectNext();
  },
  type: 'element',
};

export const IMAGE: TextMatchTransformer = {
  dependencies: [ImageNode],
  export: (node) => {
    if (!$isImageNode(node)) {
      return null;
    }

    return `![${node.getAltText()}](${node.getSrc()})`;
  },
  importRegExp: /!(?:\[([^[]*)\])(?:\(([^(]+)\))/,
  regExp: /!(?:\[([^[]*)\])(?:\(([^(]+)\))$/,
  replace: (textNode, match) => {
    const [, altText, src] = match;
    const imageNode = $createImageNode({
      altText,
      maxWidth: 800,
      src,
    });
    textNode.replace(imageNode);
  },
  trigger: ')',
  type: 'text-match',
};

export const EMOJI: TextMatchTransformer = {
  dependencies: [],
  export: () => null,
  importRegExp: /:([a-z0-9_]+):/,
  regExp: /:([a-z0-9_]+):$/,
  replace: (textNode, [, name]) => {
    const emoji = emojiList.find((e) => e.aliases.includes(name))?.emoji;
    if (emoji) {
      textNode.replace($createTextNode(emoji));
    }
  },
  trigger: ':',
  type: 'text-match',
};

export const EQUATION: TextMatchTransformer = {
  dependencies: [EquationNode],
  export: (node) => {
    if (!$isEquationNode(node)) {
      return null;
    }

    return `$${node.getEquation()}$`;
  },
  importRegExp: /\$([^$]+?)\$/,
  regExp: /\$([^$]+?)\$$/,
  replace: (textNode, match) => {
    const [, equation] = match;
    const equationNode = $createEquationNode(equation, true);
    textNode.replace(equationNode);
  },
  trigger: '$',
  type: 'text-match',
};

export const TWEET: ElementTransformer = {
  dependencies: [TweetNode],
  export: (node) => {
    if (!$isTweetNode(node)) {
      return null;
    }

    return `<tweet id="${node.getId()}" />`;
  },
  regExp: /<tweet id="([^"]+?)"\s?\/>\s?$/,
  replace: (textNode, _1, match) => {
    const [, id] = match;
    const tweetNode = $createTweetNode(id);
    textNode.replace(tweetNode);
  },
  type: 'element',
};

// Very primitive table setup
const TABLE_ROW_REG_EXP = /^(?:\|)(.+)(?:\|)\s?$/;
const TABLE_ROW_DIVIDER_REG_EXP = /^(\| ?:?-*:? ?)+\|\s?$/;

export const TABLE: ElementTransformer = {
  dependencies: [TableNode, TableRowNode, TableCellNode],
  export: (node: LexicalNode) => {
    if (!$isTableNode(node)) {
      return null;
    }

    const output: string[] = [];

    for (const row of node.getChildren()) {
      const rowOutput = [];
      if (!$isTableRowNode(row)) {
        continue;
      }

      let isHeaderRow = false;
      for (const cell of row.getChildren()) {
        // It's TableCellNode so it's just to make flow happy
        if ($isTableCellNode(cell)) {
          rowOutput.push($convertToMarkdownString(PLAYGROUND_TRANSFORMERS, cell).replace(/\n/g, '\\n').trim());
          if (cell.__headerState === TableCellHeaderStates.ROW) {
            isHeaderRow = true;
          }
        }
      }

      output.push(`| ${rowOutput.join(' | ')} |`);
      if (isHeaderRow) {
        output.push(`| ${rowOutput.map((_) => '---').join(' | ')} |`);
      }
    }

    return output.join('\n');
  },
  regExp: TABLE_ROW_REG_EXP,
  replace: (parentNode, _1, match) => {
    // Header row
    if (TABLE_ROW_DIVIDER_REG_EXP.test(match[0])) {
      const table = parentNode.getPreviousSibling();
      if (!table || !$isTableNode(table)) {
        return;
      }

      const rows = table.getChildren();
      const lastRow = rows[rows.length - 1];
      if (!lastRow || !$isTableRowNode(lastRow)) {
        return;
      }

      // Add header state to row cells
      lastRow.getChildren().forEach((cell) => {
        if (!$isTableCellNode(cell)) {
          return;
        }
        cell.setHeaderStyles(TableCellHeaderStates.ROW, TableCellHeaderStates.ROW);
      });

      // Remove line
      parentNode.remove();
      return;
    }

    const matchCells = mapToTableCells(match[0]);

    if (matchCells == null) {
      return;
    }

    const rows = [matchCells];
    let sibling = parentNode.getPreviousSibling();
    let maxCells = matchCells.length;

    while (sibling) {
      if (!$isParagraphNode(sibling)) {
        break;
      }

      if (sibling.getChildrenSize() !== 1) {
        break;
      }

      const firstChild = sibling.getFirstChild();

      if (!$isTextNode(firstChild)) {
        break;
      }

      const cells = mapToTableCells(firstChild.getTextContent());

      if (cells == null) {
        break;
      }

      maxCells = Math.max(maxCells, cells.length);
      rows.unshift(cells);
      const previousSibling = sibling.getPreviousSibling();
      sibling.remove();
      sibling = previousSibling;
    }

    const table = $createTableNode();

    for (const cells of rows) {
      const tableRow = $createTableRowNode();
      table.append(tableRow);

      for (let i = 0; i < maxCells; i++) {
        tableRow.append(i < cells.length ? cells[i] : $createTableCell(''));
      }
    }

    const previousSibling = parentNode.getPreviousSibling();
    if ($isTableNode(previousSibling) && getTableColumnsSize(previousSibling) === maxCells) {
      previousSibling.append(...table.getChildren());
      parentNode.remove();
    } else {
      parentNode.replace(table);
    }

    table.selectEnd();
  },
  type: 'element',
};

function getTableColumnsSize(table: TableNode) {
  const row = table.getFirstChild();
  return $isTableRowNode(row) ? row.getChildrenSize() : 0;
}

const $createTableCell = (textContent: string): TableCellNode => {
  textContent = textContent.replace(/\\n/g, '\n');
  const cell = $createTableCellNode(TableCellHeaderStates.NO_STATUS);
  $convertFromMarkdownString(textContent, PLAYGROUND_TRANSFORMERS, cell);
  return cell;
};

const mapToTableCells = (textContent: string): Array<TableCellNode> | null => {
  const match = textContent.match(TABLE_ROW_REG_EXP);
  if (!match || !match[1]) {
    return null;
  }
  return match[1].split('|').map((text) => $createTableCell(text));
};

export const PLAYGROUND_TRANSFORMERS: Array<Transformer> = [
  TABLE,
  HR,
  IMAGE,
  EMOJI,
  EQUATION,
  TWEET,
  CHECK_LIST,
  ...ELEMENT_TRANSFORMERS,
  ...MULTILINE_ELEMENT_TRANSFORMERS,
  ...TEXT_FORMAT_TRANSFORMERS,
  ...TEXT_MATCH_TRANSFORMERS,
];

/**
 * „É™„Çπ„ÉàË°å„ÅÆ2„Çπ„Éö„Éº„Çπ„Ç§„É≥„Éá„É≥„Éà„Çí4„Çπ„Éö„Éº„Çπ„Å´Ê≠£Ë¶èÂåñ
 * Lexical„ÅÆ„Éá„Éï„Ç©„É´„Éà„ÅØ4„Çπ„Éö„Éº„Çπ = 1„Ç§„É≥„Éá„É≥„Éà„É¨„Éô„É´
 * „Ç≥„Éº„Éâ„Éñ„É≠„ÉÉ„ÇØÂÜÖ„ÅØÂ§âÊèõ„Åó„Å™„ÅÑ
 *
 * ‚ö†Ô∏è Ê≥®ÊÑè: „Åì„ÅÆÈñ¢Êï∞„ÅØ‰ª•‰∏ã„ÅÆ„Éï„Ç°„Ç§„É´„Å´„ÇÇÂêå‰∏ÄÂÆüË£Ö„ÅåÂ≠òÂú®„Åó„Åæ„Åô„ÄÇ
 * ‰øÆÊ≠£ÊôÇ„ÅØ‰∏°Êñπ„ÇíÂêåÊúü„Åó„Å¶„Åè„Å†„Åï„ÅÑ:
 * - packages/coati-editor/src/transformers/markdown-transformers.tsÔºà„Åì„ÅÆ„Éï„Ç°„Ç§„É´Ôºâ
 * - pecus.LexicalConverter/src/lexical/transformers/markdown-transformers.ts
 *
 * @param markdown - ÂÖ•ÂäõMarkdownÊñáÂ≠óÂàó
 * @returns „É™„Çπ„Éà„Ç§„É≥„Éá„É≥„Éà„ÅåÊ≠£Ë¶èÂåñ„Åï„Çå„ÅüMarkdownÊñáÂ≠óÂàó
 */
export function normalizeListIndentation(markdown: string): string {
  const lines = markdown.split('\n');
  const result: string[] = [];
  let inCodeBlock = false;

  for (const line of lines) {
    // „Ç≥„Éº„Éâ„Éñ„É≠„ÉÉ„ÇØ„ÅÆÈñãÂßã/ÁµÇ‰∫Ü„ÇíËøΩË∑°
    if (/^```/.test(line.trim())) {
      inCodeBlock = !inCodeBlock;
      result.push(line);
      continue;
    }

    // „Ç≥„Éº„Éâ„Éñ„É≠„ÉÉ„ÇØÂÜÖ„ÅØÂ§âÊèõ„Åó„Å™„ÅÑ
    if (inCodeBlock) {
      result.push(line);
      continue;
    }

    // „É™„Çπ„ÉàË°å„ÅÆ„Åø„ÇíÂØæË±°Ôºà„É™„Çπ„Éà„Éû„Éº„Ç´„Éº„ÅåÂøÖË¶ÅÔºâ
    // - item, * item, + item, 1. item, - [ ] item, - [x] item
    const listMatch = line.match(/^(\s+)([-*+]|\d+\.)(\s+\[[ xX]?\])?\s/);
    if (listMatch) {
      const leadingSpaces = listMatch[1];
      // „Çø„Éñ„ÅØÈô§Â§ñ„Åó„Å¶„Çπ„Éö„Éº„Çπ„ÅÆ„Åø„Ç´„Ç¶„É≥„Éà
      const spaceCount = (leadingSpaces.match(/ /g) || []).length;
      const tabCount = (leadingSpaces.match(/\t/g) || []).length;

      if (spaceCount > 0 && spaceCount % 2 === 0 && spaceCount % 4 !== 0) {
        // 2„Çπ„Éö„Éº„ÇπÂçò‰ΩçÔºà„Åã„Å§4„ÅÆÂÄçÊï∞„Åß„Å™„ÅÑÔºâ‚Üí 4„Çπ„Éö„Éº„ÇπÂçò‰Ωç„Å´Â§âÊèõ
        const indentLevel = Math.ceil(spaceCount / 2);
        const newIndent = '\t'.repeat(tabCount) + '    '.repeat(indentLevel);
        result.push(newIndent + line.slice(leadingSpaces.length));
        continue;
      }
    }

    result.push(line);
  }

  return result.join('\n');
}
